
import "bootstrap/dist/css/bootstrap.css";

import { defineComponent, onBeforeUnmount } from "vue";
// import { useStore } from "vuex";
import { useStore } from "@/store/index";

import Taxonomy from "./Taxonomy/Taxonomy.vue";
import Labeler from "./Labeler/Labeler.vue";
import Browser from "./Browser/Browser.vue";

import { onMounted, computed, reactive, toRefs, h, watch } from "vue";
import { initSocket, initTrack, download } from "@/services/dataService";
import { Typing } from "@/utils/labels";

export default defineComponent({
    name: "Gallery",
    components: { Taxonomy, Labeler, Browser },
    setup() {
        const store = useStore();

        const state = reactive({});

        async function doSave(e: any) {
            if (!(e.keyCode === 83 && e.ctrlKey)) {
                return;
            }

            e.preventDefault();

            await download();
            console.log("saving from DOM!");
        }

        // Init the store to read data from backend
        onMounted(async () => {
            await store.dispatch("init");

            // setInterval(() => {
            //     // console.log('fetch');
            //     store.dispatch("saveCurrentVideo");
            // }, 2000);
            initSocket((data: DeepPartial<Typing.VideoInfo>) => {
                store.dispatch("updateFromServer", data);
            })

            initTrack((data: any) => {
                console.log("usage", data);
            })

            document.addEventListener("keydown", doSave);
        });

        onBeforeUnmount(() => {
            document.removeEventListener("keydown", doSave);
        })

        return {
            ...toRefs(state),
        };
    },
    computed: {},
});
